<template>
  <div class="classify">
    <loading v-show="isLoading" />
    <van-tabs
      v-model="index"
      title-inactive-color="#999"
      animated
      swipeable
      color="lightsteelblue"
      title-active-color="steelblue"
      sticky
    >
      <van-tab :title="v.name" class="box clearfix" v-for="(v,i) in nameList" :key="i">
        <div
          class="classify-list fl"
          v-for="(item , index) in classifyData[v.enname]"
          :key="index"
          @click="goCategory(v.enname,item.name)"
        >
          <div class="v-title">{{item.name}}</div>
          <div class="v-msg">书籍数量 : {{item.bookCount}}</div>
        </div>
      </van-tab>
    </van-tabs>
  </div>
</template>

<script>
import loading from "../../components/loading";
import "../../assets/css/classify.less";
export default {
  data() {
    return {
      index: 0,
      classifyData: {},
      nameList: [
        { name: "男生", enname: "male" },
        { name: "女生", enname: "female" },
        { name: "出版", enname: "press" },
        { name: "漫画", enname: "picture" }
      ],
      isLoading: false
    };
  },
  components: { loading },
  created() {
    this.getClassify();
  },
  methods: {
    // 获取分类数据
    getClassify() {
      this.isLoading = true
      this.axios({
        method: "get",
        url: "http://novel.kele8.cn/categories"
      })
        .then(result => {
          if (result.status == 200) {
            this.isLoading = false;
            this.classifyData = result.data;
          }
        })
        .catch(err => {
          this.isLoading = false;
        });
    },
    // 图片解码
    getImg(val) {
      return unescape(val).slice(7);
    },
    // 跳转到小分类
    goCategory(gender, major) {
      this.$router.push({
        name: "Category",
        query: { gender: gender, major: major }
      });
    }
  }
};
</script>
